"use client"

import * as React from "react"

import { cn } from "@/lib/utils"

import { DataTable } from "./data-table"

/**
 * High level container for the line dashboard feature. It keeps the
 * layout responsibilities in one place so nested sections (summary, table,
 * charts...) can be composed without the main page needing to know about
 * their internal wiring.
 */
export function LineDashboardPage({ lineId, summary, children, className }) {
  return (
    <div className={cn("flex h-full min-h-0 flex-col gap-4", className)}>
      {summary}
      <DataTable lineId={lineId} />
      {children}
    </div>
  )
}
